<template>
    <div class="home">
        <div class="djtIndex-top">
            <img src="../../assets/images/kuang-back.png" alt="" class="djtIndex-top-back">
            <div class="djtIndex-top-mean">
                <div class="djtIndex-top-mean-left">
                    <img src="../../assets/images/logo.png" alt="" class="djtIndex-top-mean-left-img">
                    <span class="djtIndex-top-mean-left-text">Donald John Trump</span>
                </div>
                <div class="djtIndex-top-mean-right">
                    <template v-if="!address">
                        <span class="djtIndex-top-mean-right-text" @click="showMask()">{{ short_address }}</span>
                    </template>
                    <template v-if="address">
                        <span class="djtIndex-top-mean-right-text">{{ short_address }}</span>
                    </template>

                    <img src="../../assets/images/index-lang.png" alt="" class="djtIndex-top-mean-right-img1" @click="showLang()">
                    <img src="../../assets/images/index-mean.png" alt="" class="djtIndex-top-mean-right-img2" @click="showMean">
                </div>
            </div>
            <div class="kuang-content">
                <div class="djtIndex-content-title">{{ lang_list.kuang_title }}</div>
                <div class="kuang-content-desc">
                    {{ lang_list.kuang_desc1 }}
                </div>
                <div class="kuang-content-detail" @click="showKuang">
                    {{ lang_list.kuang_detail }} >
                </div>
                <div class="djtIndex-content-title">{{ lang_list.kuang_desc2 }}</div>
                <div class="kuang-content-number">
                    {{ lang_list.kuang_desc3 }}
                </div>
                <div>
                    <input type="number" :placeholder="lang_list.kuang_desc4" class="kuang-content-input" v-model="activeNum" @keyup="tochangeNum()"
                           @afterpaste="checkInt()"/>
                </div>
                <div class="kuang-content-input-desc">{{ lang_list.kuang_desc5 }}{{ activeDjt }}，{{ lang_list.kuang_desc6 }}{{ activeUsdt }}</div>
                <div class="kuang-content-number">
                    {{ lang_list.kuang_time }}
                </div>
                <div class="kuang-content-button">
                    <div :class="activeTime==1?'kuang-content-button1':'kuang-content-button2'" @click="switchTime(1)">25 {{ lang_list.kuang_day }}</div>
                    <div :class="activeTime==2?'kuang-content-button1':'kuang-content-button2'" @click="switchTime(2)">49 {{ lang_list.kuang_day }}</div>
                </div>
                <div class="kuang-content-submit" @click="submitKuang($event)">{{ lang_list.kuang_button }}</div>
            </div>
        </div>

        <div v-if="meanShow" class="index_pop_content">
            <div class="index_pop_top">
                <div>
                    <img src="../../assets/images/logo.png" alt="" class="index_pop_top-left">
                </div>
                <div @click="closeMean">
                    <img src="../../assets/images/close.png" alt="" class="index_pop_top-right">
                </div>
            </div>
            <div class="index_pop_main_tab">
                <div class="index_pop_middle"  @click="toIndex()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-home.png" alt="">
                        <span>{{ lang_list.index_mean1 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toBuyDjt()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/duihuan.png" alt="">
                        <span>{{ lang_list.index_mean2 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toKuang()">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-kuang.png" alt="">
                        <span>{{ lang_list.index_mean3 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toOrder">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-record.png" alt="">
                        <span>{{ lang_list.index_mean4 }}</span>
                    </div>
                </div>
                <div class="index_pop_middle" @click="toWhite">
                    <div class="index_pop_middle_left">
                        <img src="../../assets/images/index-white.png" alt="">
                        <span>{{ lang_list.index_mean5 }}</span>
                    </div>
                </div>
                <div class="index_pop_bottom">
                    <div class="index_pop_bottom-left" @click="toTwitte()"><img src="../../assets/images/Twitte.png" alt=""></div>
                    <div class="index_pop_bottom-left" @click="todian()"><img src="../../assets/images/dianbao.png" alt=""></div>
                </div>
            </div>
        </div>
        <div v-if="maskShow" class="index_mask">
            <div class="index_mask-content" @click="checkNet()">
                <div class="index_mask-title">{{ lang_list.index_mask }}</div>
                <img src="../../assets/images/matemask.png" alt="" class="index_mask-img">
            </div>
            <div @click="closeMask()">
                <img src="../../assets/images/closed.png" alt="" class="index_mask-close">
            </div>
        </div>
        <div v-if="langShow" class="index_lang">
            <div :class="lang=='zh'?'index_lang-button-active':'index_lang-button'" @click="switchLang('zh')">中文</div>
            <div :class="lang=='en'?'index_lang-button-active':'index_lang-button'" @click="switchLang('en')">EN</div>
        </div>
        <div v-if="kuangShow" class="index_kuang">
            <div class="index_kuang-content">
                <div class="index_kuang-title">{{ lang_list.kuang_chi }}</div>
                <div class="index_kuang-desc">
                    {{ lang_list.kuang_item1 }}<br/> <br/>
                    {{ lang_list.kuang_item2 }}<br/> <br/>
                    {{ lang_list.kuang_item3 }} <br/> <br/>
                    {{ lang_list.kuang_item4 }} <br/> <br/>
                    {{ lang_list.kuang_item5 }} <br/> <br/>
                    {{ lang_list.kuang_item6 }} <br/> <br/>
                    {{ lang_list.kuang_item7 }}
                </div>
            </div>
            <div @click="closeKuang()">
                <img src="../../assets/images/closed.png" alt="" class="index_mask-close">
            </div>
        </div>
    </div>
</template>
<script>
    import {WOW} from 'wowjs';
    let provider, signer, defaultAccount, networkVersion;
    let usdtContract,djtContract,conContract;
    let baseUrl = "";
    export default {
        data() {
            return {
                lang:'zh',
                lang_list:{},
                langShow:false,
                address: '',
                short_address: 'connect',
                parents:'',
                meanShow: false,
                maskShow:false,
                kuangShow:false,
                djtBanner:0,
                usdtBanner:0,
                activeNum:'',
                activeDjt:0,
                activeUsdt:0,
                activeTime:1,
                rate:0.00125,
                disable:false,
            }
        },
        created: function () {

        },
        async mounted() {
            this.lang = sessionStorage.getItem('lang')?sessionStorage.getItem('lang'):'en';
            this.lang_list = this.en;
            switch(this.lang){
                case 'zh':
                    this.lang_list = this.zh;
                    break;
                case 'en':
                    this.lang_list = this.en;
                    break;
            }
            this.checkNet();
            var wow = new WOW(
                {
                    boxClass: 'wow',      // animated element css class (default is wow)
                    animateClass: 'animated', // animation css class (default is animated)
                    offset: 0,          // distance to the element when triggering the animation (default is 0)
                    mobile: true,       // trigger animations on mobile devices (default is true)
                    live: true,       // act on asynchronously loaded content (default is true)
                    callback: function (box) {
                        // the callback is fired every time an animation is started
                        // the argument that is passed in is the DOM node being animated
                    },
                    scrollContainer: null // optional scroll container selector, otherwise use window
                }
            );
            wow.init();
        },
        methods: {
            async submitKuang(event){
                let _this = this;
                if(this.activeDjt>this.djtBanner){
                    _this.$toast(_this.lang_list.kuang_warn1);
                    return false;
                }
                if(this.activeUsdt<=0||this.activeUsdt>this.usdtBanner){
                    _this.$toast(_this.lang_list.kuang_warn2);
                    return false;
                }
                if(this.disable){
                    return false;
                }
                this.disable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try{
                    const gasPrice = await provider.getGasPrice()
                    let etherNum = ethers.utils.parseEther (_this.activeUsdt.toString());
                    await djtContract.allowance(defaultAccount,_this.api.conAddress).then(async res =>{
                        usdtContract.allowance(defaultAccount,_this.api.conAddress).then(async res1 =>{
                                if(parseFloat(ethers.utils.formatEther(res))>0&&parseFloat(ethers.utils.formatEther(res1))>0){
                                    let res2 = await conContract.investment(etherNum,_this.activeTime,{
                                        gasPrice: gasPrice,
                                        gasLimit:800000
                                    });
                                    if(res2){
                                        _this.disable = false;
                                        _this.$toast(_this.lang_list.kuang_warn3);
                                        setTimeout(function () {
                                            location.reload();
                                        },3000)
                                    }
                                }else if(parseFloat(ethers.utils.formatEther(res))>0){
                                    let tx = await usdtContract.approve(_this.api.conAddress,'0xfffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff',{
                                        gasPrice: gasPrice,
                                        gasLimit:250000
                                    });
                                    if(tx){
                                        let res2 = await conContract.investment(etherNum,_this.activeTime,{
                                            gasPrice: gasPrice,
                                            gasLimit:800000
                                        });
                                        if(res2){
                                            _this.disable = false;
                                            _this.$toast(_this.lang_list.kuang_warn3);
                                            setTimeout(function () {
                                                location.reload();
                                            },3000)
                                        }
                                    }
                                }else if(parseFloat(ethers.utils.formatEther(res1))>0){
                                    let tx = await djtContract.approve(_this.api.conAddress,'0xfffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff',{
                                        gasPrice: gasPrice,
                                        gasLimit:250000
                                    });
                                    if(tx){
                                        let res2 = await conContract.investment(etherNum,_this.activeTime,{
                                            gasPrice: gasPrice,
                                            gasLimit:800000
                                        });
                                        if(res2){
                                            _this.disable = false;
                                            _this.$toast(_this.lang_list.kuang_warn3);
                                            setTimeout(function () {
                                                location.reload();
                                            },3000)
                                        }
                                    }
                                }else{
                                    let tx = await djtContract.approve(_this.api.conAddress,'0xfffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff',{
                                        gasPrice: gasPrice,
                                        gasLimit:250000
                                    });
                                    let tx1 = await usdtContract.approve(_this.api.conAddress,'0xfffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff',{
                                        gasPrice: gasPrice,
                                        gasLimit:250000
                                    });
                                    if(tx&&tx1){
                                        let res2 = await conContract.investment(etherNum,_this.activeTime,{
                                            gasPrice: gasPrice,
                                            gasLimit:800000
                                        });
                                        if(res2){
                                            _this.disable = false;
                                            _this.$toast(_this.lang_list.kuang_warn3);
                                            setTimeout(function () {
                                                location.reload();
                                            },3000)
                                        }
                                    }
                                }
                        }).catch(err => {
                            _this.disable = false;
                            _this.$toast(_this.lang_list.kuang_warn4);
                            setTimeout(function () {
                                location.reload();
                            },3000)
                            return false;
                        });
                    }).catch(err => {
                        _this.disable = false;
                        _this.$toast(_this.lang_list.kuang_warn4);
                        setTimeout(function () {
                            location.reload();
                        },3000)
                        return false;
                    });
                }catch (e) {
                    _this.$toast(_this.lang_list.kuang_warn4);
                    _this.disable = false;
                    setTimeout(function () {
                        location.reload();
                    },3000)
                    return false;
                }
            },
            checkInt(){
                if(this.activeNum.length==1){
                    this.activeNum=this.activeNum.replace(/[^1-9]/g,'0');
                }else{
                    this.activeNum=this.activeNum.replace(/\D/g,'');
                }
            },
            tochangeNum(){
                if(this.activeNum.length==1){
                    this.activeNum=this.activeNum.replace(/[^1-9]/g,'0');
                }else{
                    this.activeNum=this.activeNum.replace(/\D/g,'');
                }
                if(this.activeNum>0){
                    this.activeUsdt = parseFloat(this.activeNum)/2;
                    this.activeDjt = parseFloat(this.activeUsdt)*parseFloat(this.rate);
                }else{
                    this.activeDjt=0;
                    this.activeUsdt=0;
                }
            },
            switchTime(time){
                this.activeTime = time;
            },
            todian(){
                window.open('https://t.me/DJT_Group','_self');
            },
            toTwitte(){
                window.open('https://twitter.com/DJTsystem','_self');
            },
            toBuyDjt(){
                window.open('https://pancakeswap.finance/swap?outputCurrency=0x41D8675E6C64939c3245908EEaf827A995Fc47Ff&inputCurrency=0x55d398326f99059ff775485246999027b3197955&utm_source=tokenpocket','_self');
            },
            toContract(){
                window.open('https://bscscan.com/address/'+this.api.djtAddress,'_self');
            },
            toWhite(){
                window.open('/static/djt.pdf');
            },
            closeKuang(){
                this.kuangShow = false;
            },
            showKuang(){
                this.kuangShow = true;
            },
            toIndex(){
                this.$router.push({
                    name: 'home',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            toOrder(){
                this.$router.push({
                    name: 'order',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            toKuang(){
                this.$router.push({
                    name: 'kuang',
                    params: {

                    }
                })
                this.meanShow = false;
            },
            switchLang(type){
                switch(type){
                    case 'zh':
                        sessionStorage.setItem('lang','zh');
                        break;
                    case 'en':
                        sessionStorage.setItem('lang','en');
                        break;
                }
                location.reload();
            },
            //显示菜单
            showLang(){
                if(this.langShow){
                    this.langShow = false;
                }else{
                    this.langShow = true;
                }

            },
            //显示菜单
            showMask(){
                this.maskShow = true;
            },
            //关闭菜单
            closeMask(){
                this.maskShow = false;
            },
            //显示菜单
            showMean(){
                this.meanShow = true;
            },
            //关闭菜单
            closeMean(){
                this.meanShow = false;
            },
            connectWallet() {
                if (this.address) {
                    this.short_address = this.address.substr(0, 3) + '...' + this.address.substr(this.address.length - 3)
                } else {
                    this.checkNet()
                }
            },
            getUrlParam(name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
                var r = window.location.search.substr(1).match(reg);  //匹配目标参数
                if (r != null) return unescape(r[2]);
                return null; //返回参数值
            },
            async checkNet() {
                let _this = this;

                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin();
                } catch (e) {
                    _this.$toast(_this.lang_list.index_warn);
                }

            },
            async checkLogin() {
                let _this = this;
                setInterval(function () {
                    window.ethereum.enable()
                        .then(function (accounts) {
                            if (accounts.length == 0 && defaultAccount != "") {
                                window.location.reload();
                                return;
                            }
                            if (accounts.length == 0) {
                                window.location.reload();
                                return;
                            }
                            if (accounts[0] != defaultAccount) {
                                defaultAccount = accounts[0];
                                window.location.reload();
                                console.log("account change, start bat updateData!")
                            }
                        })
                        .catch(function (reason) {
                            // Handle error. Likely the user rejected the login:
                            console.log(reason === "User rejected provider access")
                        })
                }, 1000);
                window.ethereum.enable()
                    .then(async function (accounts) {
                        if (accounts.length == 0) {
                            console.log('33333');
                            return;
                        }
                        defaultAccount = accounts[0];
                        console.log("defaultAccount: ", defaultAccount);
                        networkVersion = window.ethereum.networkVersion
                        if (networkVersion == 1) {  //以太坊主网
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 42) {   //以太坊测试网  K
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 128) {  //HECO主网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 256) { //HECO测试网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        else if (networkVersion == 56) { //币安主网
                            _this.type = 'BNB';
                        }
                        else {
                            _this.$toast(_this.lang_list.index_warn);
                            return false;
                        }
                        usdtContract = new ethers.Contract(_this.api.usdtAddress, usdtAbi, signer);
                        djtContract = new ethers.Contract(_this.api.djtAddress, djtAbi, signer);
                        conContract = new ethers.Contract(_this.api.conAddress, conAbi, signer);
                        if (defaultAccount != '') {
                            _this.address = defaultAccount;
                            _this.getBanner();
                        }
                    });
                setTimeout(function () {
                    if (defaultAccount) {
                        _this.address = defaultAccount;
                        _this.short_address = _this.address.substr(0, 3) + '...' + _this.address.substr(_this.address.length - 3)
                    }

                }, 500)
            },
            async getBanner(){
                let _this = this;
                await usdtContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.usdtBanner = Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                    console.log(_this.usdtBanner)
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await djtContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.djtBanner = Math.floor(parseFloat(ethers.utils.formatEther(res)*1000000000) * 100) / 100;
                    console.log(_this.djtBanner)
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await conContract.Price().then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.rate = parseFloat(ethers.utils.formatEther(res)*10000000000000);
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
        }
    }
</script>


